.video {
  margin: 2% 0px 0px 0px;
  width: 100%;
}

.card_index {
  font-size: 1.2vm;
  width: 60vw;
}

.ant-card-body {
  background-color: #fff;
  padding-top: 1%;
  padding-bottom: 1%;
  font-size: 1.1vw;
  color: rgb(102, 101, 101);
}

.notice-noout_index {
  margin-top: 0px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.left-text-project {
  color: #515a6e;
  font-size: 1.5vw;
}
.left-text-introduce {
  color: #515a6e;
  font-size: 1.5vw;
}
.left_img {
  margin-bottom: 0.5vw;
  width: 1.5vw;
}

.project-img {
  width: 100%;
}
.project-img:hover {
  box-shadow: 2px 8px 4px rgba(91, 163, 245, 0.904),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}
.project_content {
  font-size: 1.2vw;
  font-weight: bolder;
  color: #515a6e;
}

.link {
  font-size: 1.2vw;
}
.introduct {
  margin-top: 3%;
  font-size: 1.5vw;
  color: gray;
}
.img_notice {
  width: 1.5vw;
}
.col_introduce {
  margin-top: 3%;
}
.slick-prev:before,
.slick-next:before {
  display: none;
}
.ant-col
  ant-col-xs-18
  ant-col-xs-offset-3
  ant-col-sm-18
  ant-col-sm-offset-6
  ant-col-md-24
  ant-col-md-offset-5
  ant-col-lg-24
  ant-col-lg-offset-5
  ant-col-xl-15
  ant-col-xl-offset-0 {
  border-radius: 20%;
}
.btn_honor {
  margin-left: 50vw;
}
